import React from "react";
import {withStyles} from "@material-ui/core";
import Container from "@material-ui/core/Container";
import Item from "./Item";

const styles = theme => ({});

const items = [
    {
        startTime: 0, endTime: 50,
        title: "One", description: "Have lunch",
    },
    {
        startTime: 50, endTime: 100,
        title: "Two", description: "Have lunch",
    },
    {
        startTime: 100, endTime: 200,
        title: "Three", description: "Have lunch",
    },
    {
        startTime: 200, endTime: 300,
        title: "Four", description: "Have lunch",
    },
    {
        startTime: 300, endTime: 400,
        title: "Five", description: "Have lunch",
    },
    {
        startTime: 400, endTime: 500,
        title: "Six", description: "Have lunch",
    },
    {
        startTime: 500, endTime: 600,
        title: "Seven", description: "Have lunch",
    },
    {
        startTime: 600, endTime: 700,
        title: "Eight", description: "Have lunch",
    },
    {
        startTime: 700, endTime: 800,
        title: "Nine", description: "Have lunch",
    },
    {
        startTime: 800, endTime: 900,
        title: "Ten", description: "Have lunch",
    },
    {
        startTime: 900, endTime: 1000,
        title: "Eleven", description: "Have lunch",
    },
    {
        startTime: 1000, endTime: 1440,
        title: "Twelve", description: "Have lunch",
    },
];

class Comp extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            items: items,
            active: {}
        };
    }

    render() {
        const {items, active} = this.state;
        return (
            <Container maxWidth={"md"}>
                {
                    items.map((item, i) =>
                        <Item
                            item={item}
                            active={active[i]}
                            key={i}
                        />
                    )
                }
            </Container>
        );
    }

    componentDidMount() {
        const timeout = 60 * 1000;
        this.updateCompleted();
        this.updateTimer = setInterval(this.updateCompleted.bind(this), timeout);
    }

    componentWillUnmount() {
        clearInterval(this.updateTimer);
    }

    updateCompleted() {
        const {items} = this.state;
        const newItems = [];
        const active = {};
        const d = new Date();
        let curr = d.getHours() * 60 + d.getMinutes();
        for (let i = 0; i < items.length; ++i) {
            let completed = -1, start = items[i].startTime, end = items[i].endTime;
            if (start <= curr && curr < end)
                completed = Math.floor(100 * (curr - start) / (end - start));
            if (completed !== -1) {
                active[i] = true;
                newItems.push({...items[i], completed});
            } else {
                active[i] = false;
                newItems.push(items[i]);
            }
        }
        this.setState({items: newItems, active});
    }
}

export default withStyles(styles, {withTheme: true})(Comp);